.button-next {
  background-image: url(../../../static/img/rectangle-8-1.svg);
  background-size: 100% 100%;
  box-shadow: 0px 4px 4px #00000040;
  height: 30px;
  position: relative;
  width: 79px;
}

.button-next .next {
  color: #ebebd3;
  font-family: "Inter", Helvetica;
  font-size: 11px;
  font-weight: 300;
  left: 27px;
  letter-spacing: 0;
  line-height: normal;
  position: absolute;
  text-align: justify;
  top: 7px;
  white-space: nowrap;
}
